
<template>
  <form @submit.prevent="submit" class='storeInformation h-adr'>
    <h4></h4>
    <Textbox v-model="store_name" placeholder="" required/>
    
    <h4></h4>
    <TextboxNumber v-model="seating_capacity" placeholder="" required/>
    
    <h4></h4>
    <RadioButton v-model="takeout_support" :radioButtonOptions="radioButtonTakeoutSupport"/>

    <h4></h4>
    <PostalCode 
      v-model:postal_code="store_postal_code"
      @yubinbango='yubinbango'
    />
    <h4></h4>
    <Textbox type="text" v-model="store_address1" placeholder="" disabled required/>
    <h4></h4>
    <Textbox type="text" v-model="store_address2" placeholder="" disabled required/>
    <h4></h4>
    <Textbox type="text" v-model="store_address3" placeholder="" disabled required/>
    <h4></h4>
    <Textbox type="text" v-model="store_address4" placeholder="" required/>
    <h4></h4>
    <Textbox type="text" v-model="store_address5" placeholder=""/>

    <SubmitButton type="submit"></SubmitButton>
  </form>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import { Core as YubinBangoCore } from 'yubinbango-core'
import Textbox from "../atoms/Textbox"
import TextboxNumber from "../atoms/TextboxNumber"
import RadioButton from "../atoms/RadioButton"
import SubmitButton from "../atoms/SubmitButton"
import PostalCode from "../molecules/PostalCode"

export default {
  data: () => {
    return {
      store_name: "",
      seating_capacity: "",
      takeout_support: "false",
      store_postal_code: null,
      store_address1: null,
      store_address2: null,
      store_address3: null,
      store_address4: null,
      store_address5: null,
      radioButtonTakeoutSupport: [
        {id: 'takeout_support--false', value: 'false', name: 'takeout_support', label: ''},
        {id: 'takeout_support--true', value: 'true', name: 'takeout_support', label: ''}
      ],
    }
  },
  components: {
        Textbox,
        TextboxNumber,
        RadioButton,
        SubmitButton,
        PostalCode,
  },
  computed: {
    ...mapGetters([ 'storeName', 'storeID', 'seatingCapacity', 'takeoutSupport'])
  },
  methods: {
    ...mapActions([ 'storeInformation_update' ]),
    submit(submitEvent){
      const store_id = this.storeID
      const store_name = submitEvent.target.elements.store_name.value;
      const seating_capacity= submitEvent.target.elements.seating_capacity.value;
      const takeout_support = submitEvent.target.elements.takeout_support.value;
      const storeInformation = {
        'store_id' :store_id,
        'store_name' :store_name,
        'seating_capacity' :seating_capacity,
        'takeout_support' :takeout_support,
      }
      this.storeInformation_update(storeInformation)
    },
    yubinbango() {
      console.log('yubinbango')
      console.log(this.store_postal_code)
      this.store_postal_code = this.halfSizeNumberFormat(this.store_postal_code)
      console.log('yubinbango')
      new YubinBangoCore(this.store_postal_code, (addr)=> {
        this.store_address1 = addr.region // 
        this.store_address2 = addr.locality// 
        this.store_address3 = addr.street // 
      })
    },
    halfSizeNumberFormat(number) {
      return String(number).replace(/[-]/g,s => String.fromCharCode(s.charCodeAt(0) - 65248)).replace(/\D/g,'')
    },
  }
}
</script>

<style lang="scss" scoped>
.storeInformation{
  display: flex;
  width: 280px;
  flex-flow: wrap;
  margin: 50px auto 50px;
  &>:nth-child(n){
    margin-bottom: 5px;
  }
  &::after{
    @include after;
  }
  &>button{
    margin-top:15px;
  }
}
</style>
